import Visualization from './visualization/visualization';
import Circle from './visualization/circle';

const data = [
  { x: 25, y: 36 },
  { x: 56, y: 73 },
  { x: 65, y: 135 },
  { x: 75, y: 103 },
  { x: 173, y: 64 },
  { x: 44, y: 122 },
  { x: 47, y: 38 },
  { x: 346, y: 10 }
];
const circles = data.map(d => new Circle(d.x, d.y));

const vis = new Visualization('body', 400, 200, circles);

vis.plotData();
