import * as d3 from 'd3';

export default class Visualization {
  constructor(rootNode = 'body', width = 500, height = 300, data, n = 100) {
    this.svg = d3.select(rootNode)
      .append('svg')
      .style('fill', 'darkgrey')
      .attr('width', width)
      .attr('height', height)
      .attr('id', 'd3ml');
  }
  createSVG(rootNode, width, height) {
    return d3.select(rootNode)
      .append('svg')
      .style('fill', 'darkgrey')
      .attr('width', width)
      .attr('height', height)
      .attr('id', 'd3ml');
  }
  plotData(data, n) {
    if (data) {
      this.n = data.length;
      this.drawCircles(data);
    }
    // todo: else plot random data
  }
  drawCircles(data) {
    this.svg.selectAll('circle')
      .data(data)
      .enter().append('circle')
      .style('stroke', function (d) { return d.stroke; })
      .style('fill', function (d) { return d.fill; })
      .attr('r', function (d) { return d.radius; })
      .attr('cx', function (d) { return d.cx; })
      .attr('cy', function (d) { return d.cy; });
  }
}
